html,
body,
#root {
  height: 100%;
}

/* Scale down the UI to show more content (like 80% browser zoom) */
.pf-v6-c-compass__content {
  transform: scale(0.85);
  transform-origin: top left;
  width: 117.65%; /* 100% / 0.85 to maintain full width after scaling */
  min-height: calc(100vh / 0.85); /* Expand height to fill viewport after scaling */
}

/* Scale card content back to original size (counter-scale) */
.pf-v6-c-card {
  transform: scale(1.176); /* 1 / 0.85 to restore original content size */
  transform-origin: top left;
}

/* Adjust card grid to have larger gaps to account for scaled cards overflowing their cells */
/* Cards are 17.6% larger, so we need ~55px gap to end up with 24px visual spacing */
.pf-v6-c-compass__content [style*="display: grid"] {
  column-gap: 80px !important;
  row-gap: 80px !important;
}

.pf-v6-c-content {
  --pf-v6-c-content--small--Color: var(--pf-t--global--color--status--danger--default); /* changes all <small> color to the semantic token for danger */
  --pf-v6-c-content--blockquote--BorderLeftColor: var(--pf-t--global--color--nonstatus--purple--default); /* changes all <blockquote> left border color to the semantic token for non-status (purple) */
  --pf-v6-c-content--hr--BackgroundColor: var(--pf-t--global--color--nonstatus--yellow--default); /* changes a <hr> color to the semantic token for non-status (yellow) */
}

/* Force isThinking animation to work regardless of reduced-motion preference */
:root {
  --pf-v6-global--thinking-active--Animation--Duration: 2s;
}

/* White background for ClipboardCopy components */
.clipboard-white-bg .pf-v6-c-clipboard-copy {
  background-color: var(--pf-t--global--background--color--primary--default);
}

.clipboard-white-bg .pf-v6-c-clipboard-copy__text {
  background-color: var(--pf-t--global--background--color--primary--default);
}

/* Light background for ToggleGroup items */
.pf-m-light-background .pf-v6-c-toggle-group__button:not(.pf-m-selected) {
  background-color: var(--pf-t--global--background--color--primary--default);
}

/* Bottom drawer styling */
.pf-v6-c-drawer.pf-m-panel-bottom .pf-v6-c-drawer__panel {
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  box-shadow: var(--pf-t--global--box-shadow--lg);
}

/* Ensure drawer panel content fills the panel */
.pf-v6-c-drawer.pf-m-panel-bottom .pf-v6-c-drawer__panel-main {
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  overflow: hidden;
}
